
  var lrz = require("lrz");
  var $ = require("jquery");
  window.jQuery = $;

  var CosCloud = require("../../../static/lib/js/cos-js-sdk-v4");
  var myFolderName = function () {
    var now = new Date();
    var nowYear = now.getFullYear();
    var nowMonth = now.getMonth() + 1;
    if(nowMonth < 10) {
      nowMonth = '0' + nowMonth;
    } else {
      nowMonth = String(nowMonth);
    }
    return '/image/' + nowYear + '-' + nowMonth + '/'
  };

  import Icon from 'vux/src/components/icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
;

  export default {
    props: ['imageData'], //: {"url": "xxxxx", "id": 123}
    data() {
      return {
        data: {
          noticeText: '',
          imgList: [],
          nowImgUrl: '',
          nowImgWidth: 0,
          nowImgHeight: 0,
          nowImgFile: undefined
        },
        uploadState: {
          uploadSuccess: false,
          uploadFalse: false,
          uploading: false,
          uploadingTxt: null,
        },
        cosData: {
          bucket: 'webstatic',
          appid: '1252911372',
          sid: '',
          skey: '',
          region: 'gz',
          myFolder: myFolderName(),
          fileName: function () {
            var guid = "";
            for (var i = 1; i <= 32; i++) {
              var n = Math.floor(Math.random() * 16.0).toString(16);
              guid += n;
              if ((i == 8) || (i == 12) || (i == 16) || (i == 20))
                guid += "-";
            }
            return guid;
          },
          periodSign: '',
          onceSign: '',
          cosObject: null,

        }
      }
    },
    methods: {
      successCallBack: function (result) {
        console.log("==========,img url==========");
        console.log(result);

        this.uploadState.uploadingTxt = false;
        this.uploadState.uploadingTxt = 0

        var image = {
          url: result.data.access_url.replace(/^http:\/\/[\w\d\-\_.]+\.com/i, ""),
          width: this.data.nowImgWidth,
          height: this.data.nowImgHeight,
          offsetX: 0
        };

        console.log("==========image===========");
        console.log(image);

        //imgIDimage{url,width,height,offsetX}
        if (!this.imageData.isGetImageId) {
          this.imageData.onlyImage = image;
        } else {
          var self = this;
          //
          self.H.toAjax(true).then(function (data) { //data 
            var submitData = data;
            submitData.data.image = image;
            self.A.post("/v1/common/uploadImage", submitData).then(function (result) {
              console.log("===,imgid,urlvue.imageData.image===");
              image.id = result.data.data.imageId;
              self.imageData.onlyImage = image;
              self.uploadState.uploadSuccess = true;
            });
          });
        }
      },
      errorCallBack: function (result) {
        console.log("====================");
        this.uploadState.uploadFalse = true;
        if (this.data.imgList.length > 0) {
          this.data.imgList.pop();
        }
      },
      progressCallBack: function (curr) {
        console.log(':' + curr * 100 + "%");
        this.uploadState.uploading = true;
        this.uploadState.uploadingTxt = Math.floor(curr * 100) + "%";
        if (curr === 1) {
          this.uploadState.uploading = false;
        }
      },
      //input[file]
      addFile(e){
        var self = this;
        self.uploadState.uploading = true;
        self.uploadState.uploadingTxt = "";
        //
        var promise = new Promise(function (resolve, reject) {
          for (var i = 0; i < e.target.files.length; i++) {
            console.log("======");
            console.log("", e.target.files[i].size);
            console.log("", e.target.files[i].type);
            lrz(e.target.files[0])
              .then(function (rst) {
                // 
                console.log(rst);
                var image = new Image();
                image.src = rst.base64; // base64 
                image.onload = function () { // this => imgObject
                  self.data.nowImgUrl = rst.base64;
                  self.data.nowImgFile = rst.file;
//                  self.data.imgList.push(rst);
                  self.data.nowImgWidth = image.width;
                  self.data.nowImgHeight = image.height;
//                  console.log(self.data.nowImgWidth = image.width);
//                  console.log(self.data.nowImgHeight = image.height);
                  console.log("", rst);
                  console.log("", rst.fileLen);
                  console.log("", rst.file.type);
                  resolve(false);
                }
              })
              .catch(function (err) {
                // 
                console.log(err);
                reject(false);
              })
              .always(function () {
                // 
                self.uploadState.uploading = false;
              });
          }
        });

        //cos + 
        promise.then(function (successData) {
          console.log("==========: " + successData);
          self.getCos();
          self.uploadImageToCos();
        }, function (falseData) {

        });
      },
      //coscos
      getCos(){
        if (this.cosData.cosObject == null) {
          console.warn("===coscos===");
          var _self = this;
          var cosObject = new CosCloud({
            appid: this.cosData.appid,// APPID 
            bucket: this.cosData.bucket,//bucketName 
            region: this.cosData.region,
            getAppSign: function (callback) {
              var signCode = "";
              _self.H.toAjax(true).then(function (data) { //data 
                var submitData = data;
                _self.A.post("/v1/common/cos-sign", submitData).then(function (result) {
                  console.log(result.data.data);
                  signCode = result.data.data.periodSign;
                  callback(signCode);
                });
              });
            },
            getAppSignOnce: function (callback) {
              var signCode = "";
              _self.H.toAjax(true).then(function (data) { //data 
                var submitData = data;
                _self.A.post("/v1/common/cos-sign", submitData).then(function (result) {
                  console.log(result.data.data);
                  signCode = result.data.data.onceSign;
                  callback(signCode);
                });
              });
            }
          });

          this.cosData.cosObject = cosObject;
        }
      },
      //
      uploadImageToCos(){
        var _self = this;
        console.warn("======");
        this.cosData.cosObject.uploadFile(
          _self.successCallBack,
          _self.errorCallBack,
          _self.progressCallBack,
          _self.cosData.bucket,
          _self.cosData.myFolder + _self.cosData.fileName() + ".jpg",
          _self.data.nowImgFile, //
          1
        );
      },
      //
      deleteImage(index){
        this.data.imgList.splice(index, 1);
        this.imageData.image.pop();
      },
//      //
//      checkFileNums(e){
//        //
//        if (this.data.imgList.length >= 1) {
//          alert("");
//          e.cancelable;
//          e.stopPropagation();
//          e.preventDefault();
//          return false;
//        }
//      },
    },
    components: {
      icon: Icon,
      toast: Toast
    }
  }



